export default class VideoHeartbeatHandler {
    constructor(tabRegistry) {
        this.tabRegistry = tabRegistry;
    }
    get sender() {
        return 'asbplayer-video';
    }
    get command() {
        return 'heartbeat';
    }
    handle(command, sender) {
        const videoToExtensionCommand = command;
        if (sender.tab) {
            this.tabRegistry.onVideoElementHeartbeat(sender.tab, videoToExtensionCommand.src, videoToExtensionCommand.message);
        }
        return false;
    }
}
