export default class PublishCardHandler {
    constructor(cardPublisher) {
        this._cardPublisher = cardPublisher;
    }
    get sender() {
        return 'asbplayerv2';
    }
    get command() {
        return 'publish-card';
    }
    handle(command, sender) {
        const message = command.message;
        this._cardPublisher.publish(message);
        return false;
    }
}
