const blobUrls = {};
export const createBlobUrl = (file) => {
    const blobUrl = URL.createObjectURL(file);
    blobUrls[blobUrl] = true;
    return blobUrl;
};
export const addBlobUrl = (blobUrl) => {
    blobUrls[blobUrl] = true;
};
export const revokeBlobUrl = (blobUrl) => {
    delete blobUrls[blobUrl];
    URL.revokeObjectURL(blobUrl);
};
export const isActiveBlobUrl = (blobUrl) => {
    return blobUrl in blobUrls;
};
