export default class RequestSubtitlesHandler {
    get sender() {
        return 'asbplayerv2';
    }
    get command() {
        return 'request-subtitles';
    }
    handle(command, sender, sendResponse) {
        const { tabId, src } = command;
        const requestSubtitlesFromTabCommand = {
            sender: 'asbplayer-extension-to-video',
            src,
            message: {
                command: 'request-subtitles',
            },
        };
        chrome.tabs.sendMessage(tabId, requestSubtitlesFromTabCommand).then((response) => {
            sendResponse(response);
        });
        return true;
    }
}
