export default class MobileOverlayForwarderHandler {
    get sender() {
        return 'asbplayer-mobile-overlay-to-video';
    }
    get command() {
        return null;
    }
    handle(command, sender, sendResponse) {
        if (sender.tab?.id === undefined) {
            return;
        }
        chrome.tabs.sendMessage(sender.tab.id, command);
        return false;
    }
}
